#skills>header>div {
    font-size: 35px;
    font-weight: bold;
    padding: 10px;
    padding-top: 30px;
}

#skills {
    background-color: rgb(245, 239, 239);
}

#skill-wrapper {
    padding: 10px;
    background-color: rgb(245, 239, 239);
    padding-bottom: 50px;
    border-bottom: 1px solid rgb(216, 212, 212);
}

#skill-container {
    padding: 10px 10%;
    background-color: black;
    margin: auto;
    border-radius: 10px;
    width: 70%;
    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.6);
}

#skill-container-each {
    padding-bottom: 20px;
}

.title-container {
    background-color: black;
    text-align: left;
    font-size: 20px;
    font-weight: bold;
    color: white;
    /* padding-left: 150px; */
    margin-top: 10px;
}

.progressBar-container {
    background-color: black;
    height: 20px;
    color: white;
    font-weight: 800;

}

.progressBar-base {
    background-color: rgb(48, 45, 45);
    height: 12px;
    margin: auto;
    border-radius: 5px;
}

.progressBar {
    background-color: rgb(218, 39, 39);
    height: 12px;
    border-radius: 5px;

    animation: progress 1500ms ease-in 1;
}

.percentage {
    color: black;
    background-color: white;
    height: 20px;
    width: 20px;
    position: relative;
    left: 0px;
    border-radius: 3px;
    margin-bottom: 10px;
    animation: progress 1500ms ease-in 1;
    padding-top: 4px;
}

@keyframes progress {
    0% {
        width: 0;
        /* opacity: 1; */
    }

    100% {
        /* opacity: 1; */
    }
}




